* {
  box-sizing: border-box;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 60px;
  background: #f5f5f5;
  display: flex;
  align-items: center;
  padding: 0 20px;
  justify-content: space-between;
  z-index: 1000;
  transition: background-color 0.3s ease;
}

.header--scrolled {
  background-color: #3730a3;
}

.header__logo img {
  height: 40px;
  transition: opacity 0.3s ease;
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between; /* space between links and button */
  width: 100%;
  gap: 20px;
}

.nav-links {
  display: flex;
  gap: 20px;
  justify-content: center; /* center links horizontally */
  flex: 1; /* take all available space */
}

.nav-links a {
  color: #000;
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #3730a3;
}

/* Desktop scroll: links turn white */
.header--scrolled .nav-links a {
  color: white;
}

.header--scrolled .nav-links a:hover {
  color: #bbb;
}

/* Download button styles */
.download-button {
  padding: 10px 18px;
  font-weight: 600;
  font-size: 16px;
  background-color: #3730a3;
  color: white;
  border: none;
  border-radius: 40px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease;
}

.download-button:hover {
  background-color: #2c2380;
}

/* Download button style changes when scrolled */
.header--scrolled .download-button {
  background-color: white;
  color: #3730a3;
  border: 1px solid #3730a3;
}

.header--scrolled .download-button:hover {
  background-color: #f0f0f0;
  color: #2c2380;
  border-color: #2c2380;
}

.hamburger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 24px;
  height: 18px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.hamburger span {
  display: block;
  height: 3px;
  background-color: #000;
  border-radius: 2px;
  transition: all 0.3s ease;
}

/* Hamburger bars turn white on scroll */
.header--scrolled .hamburger span {
  background-color: white;
}

.hamburger.open span:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.hamburger.open span:nth-child(2) {
  opacity: 0;
}

.hamburger.open span:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -5px);
}

/* Responsive */
@media (max-width: 768px) {
  .header__nav {
    flex-direction: column;
    align-items: flex-start;
    width: 200px;
    position: fixed;
    top: 60px;
    right: 0;
    background: #f5f5f5;
    height: calc(100vh - 60px);
    padding: 20px;
    transform: translateX(100%);
    transition: transform 0.3s ease;

    overflow-y: auto; /* enable vertical scroll inside sidebar */
    -webkit-overflow-scrolling: touch; /* smooth scrolling on iOS */
    max-height: calc(100vh - 60px); /* limit height */
  }

  .header__nav.open {
    transform: translateX(0);
  }

  /* Mobile: vertical nav links */
  .nav-links {
    flex-direction: column;
    width: 100%;
    justify-content: flex-start;
    margin-bottom: 15px;
  }

  /* Mobile: links always black regardless of scroll */
  .header--scrolled .nav-links a {
    color: black !important;
  }

  .nav-links a:hover {
    color: #3730a3;
  }

  .nav-links a,
  .download-button {
    margin-bottom: 15px;
  }

  .hamburger {
    display: flex;
  }
}
