/* Fade In Overlay */
.fadeInOverlay {
  animation: fadeInOverlay 0.3s forwards;
}

/* Fade Out Overlay */
.fadeOutOverlay {
  animation: fadeOutOverlay 0.3s forwards;
}

@keyframes fadeInOverlay {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOutOverlay {
  from { opacity: 1; }
  to { opacity: 0; }
}

/* Fade In Modal */
.fadeInModal {
  animation: fadeInModal 0.3s forwards;
}

@keyframes fadeInModal {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Fade Out Modal */
.fadeOutModal {
  animation: fadeOutModal 0.3s forwards;
}

@keyframes fadeOutModal {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-20px);
  }
}

/* Modal Styles */

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal__container {
  background: white;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  border-radius: 8px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  background: #f5f5f5;
  border-bottom: 1px solid #ddd;
}

.modal__close {
  background: transparent;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.modal__content {
  padding: 20px;
  overflow-y: auto;
  flex-grow: 1;
  font-size: 0.95rem;
  color: #333;
  line-height: 1.5;
}
